html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::selection {
  color: #abe4cd;
  background-color: #343a40;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.login {
  margin-top: 50px;
  font-family: "Poppins", sans-serif;
  color: #343a40;
}

.login .row {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 10px 10px 10px 10px;
  -moz-border-radius: 10px 10px 10px 10px;
  -webkit-border-radius: 10px 10px 10px 10px;
  border: 1px solid #dcdde1;
}

.login .row .col {
  padding: 50px;
}

.login .row .section1-login {
  background-image: linear-gradient(grey, grey),
    url(/Baha-Admin/static/media/ashim-d-silva-ZmgJiztRHXE-unsplash.0fda085e.jpg);
  background-repeat: no-repeat;
  background-size: 275%;
  background-position: 40% 50%;
  background-blend-mode: multiply;
  border-radius: 10px 0px 0px 10px;
  -moz-border-radius: 10px 0px 0px 10px;
  -webkit-border-radius: 10px 0px 0px 10px;
  color: #dcdde1;
}

.login .row .section1-login h3 {
  font-size: 16pt;
}

.login .row .section1-login h3 span {
  display: block;
  line-height: 1.75;
}

.login .row .col img {
  width: 30px;
  height: auto;
  margin-right: 5px;
}

.login .row .col h6 {
  font-size: 7pt;
  color: #00472b;
  font-style: italic;
}

.login .row .col h2 {
  font-size: 14pt;
}

.login .row .col h4 {
  font-size: 8pt;
  color: #dadada;
}

.login .row .col h5 {
  margin-bottom: 5px;
  font-size: 8pt;
}

.login .form-control {
  margin-bottom: 15px;
  padding: 20px 18px;
  border-radius: 10px 10px 10px 10px;
  -moz-border-radius: 10px 10px 10px 10px;
  -webkit-border-radius: 10px 10px 10px 10px;
  border: 0.5px solid #dcdde1;
  font-size: 10pt;
}

::-webkit-input-placeholder {
  color: #dcdde1 !important;
}

:-ms-input-placeholder {
  color: #dcdde1 !important;
}

::placeholder {
  color: #dcdde1 !important;
}

.login .row input[type="submit"] {
  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-left: auto;
  font-weight: bold;
  font-size: 10pt;
  color: #343a40;
  border: 0px solid #dcdde1;
  background: linear-gradient(#343a40, #1a1f27);
  color: #dcdde1;
  border-radius: 10px;
}

.login .row input:hover[type="submit"]:hover {
  background-image: linear-gradient(grey, grey),
    url(/Baha-Admin/static/media/charles-deluvio-FK81rxilUXg-unsplash.0a3dded3.jpg);
  background-repeat: no-repeat;
  background-size: 220%;
  background-position: 10% 50%;
  background-blend-mode: multiply;
  color: #dcdde1;
  text-decoration: none;
  transition: 0.3s;
}

.nav {
  font-family: "Poppins", sans-serif;
}

.side-nav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  /* background-color: #111; */
  background-color: #ffffff;
  overflow-x: hidden;
  padding-top: 60px;
  transition: 0.3s;
  /* background-image: linear-gradient(grey, grey),
    url(/src/assets/images/creaslim-F7eJxHoR1lg-unsplash.jpg);
  background-repeat: no-repeat;
  background-size: 250%;
  background-position: 50% 50%;
  background-blend-mode: multiply; */
}

.openbtn-nav {
  position: fixed;
  margin-top: 10px;
  margin-left: 10px;
}

.side-nav .closebtn-nav {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
  color: #dcdde1;
}

.side-nav .closebtn-nav:hover {
  color: #c6c6ca;
}

.side-nav img {
  width: 50px;
  height: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.side-nav h2 {
  font-size: 12pt;
  color: #818181;
}

.side-nav .btn-outline-danger {
  padding: 8px;
  margin-left: auto;
  margin-right: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border: 0px;
  background-color: #ff7979;
  color: #ffffff;
}

.side-nav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 16px;
  color: #343a40;
  display: block;
  transition: 0.3s;
}

.side-nav a:hover {
  color: #f1f1f1;
}

